import React from "react";
import {Link} from 'react-router-dom';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
class FooterComponent extends React.Component {
  constructor(props) {
    super(props);
    
  }

  render() {
   
    return(
      
      <Container className="footer ">
        <Grid container spacing={2} className="stencil-font" style={{textAlign:"center"}}>
          <Grid item md={4} xs={12} className="accolade-font" >
          Ⓒ 2021 The People's Republic of North Bananas
          </Grid>
          <Grid item md={4} xs={12}  className="accolade-font">
            <div className=" banana-twitter-footer">
              <a>&nbsp;&nbsp;&nbsp;</a>
            </div>
          </Grid>
          <Grid item md={4} xs={12}  className="accolade-font">
            <div className="banana-discord-footer">
              <a> &nbsp;&nbsp;&nbsp;</a>
            </div>
          </Grid>
         
         
        </Grid>
      </Container>
    )
  }
}

export default FooterComponent;
